 .cmt-section {
    background-color: #EEEEEE; 
    margin-top:8%;
}
.cmt-top{
    padding-left:13rem;
}
.cmt-heading h2 {
    padding-top:5%;
    font-size: 76px;
    font-weight:400;
    text-align: center;
    text-transform:uppercase;
}
.cmt-content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding:2%;

}
.cmmtimg img{
    height:50vh;
    width:62%;
    overflow: hidden;
    }

