@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

a{
  text-decoration:none;
}
.big-text {
    font-size:58px;
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    margin-top:15%;
    color:#5F5F5F;
    text-transform: uppercase;
    line-height:1.3;
  }
  .vertical-align {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 100%;
  }
.home-text {
  font-size:25px;
  display: flex;
  justify-content: flex-end;
  text-align: right;
}
.home-text p{
  color:#080028;
}
.home-text .content button {
  width: 40%; /* Adjust the width as needed */
  margin-left:auto;
  background:#f77700;
  color:#fff;
  font-weight:700;
}
.home-text .content{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card-view{
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.3);
}
 .icon-section{
  padding-top:40px;
 }
 .icon-section .card-view img{
  width:60%;
  padding:10%;
  }
  .icon-section p{
    font-size:14px;
  }
 .icon-section .card-view h6{
  font-weight:700;
  padding-bottom:10%;
 }
 
 .banner {
  background-image: url('../../assets/icon/whoarewe.jpg');
  background-size: cover;
  height: 50vh; /* adjust height according to your requirement */
}
 .banner img {
  max-width: 100px;
  max-height: 100px;
 }
 ul li {
  list-style:none;
 }
 .banner-texthalf{
  padding:2% 8%;
 }
 .banner-texthalf h2{
  font-weight:700;
  color:#080028;
 }
 .banner-texthalf p{
  font-size:20px;
 }
 
 .banner-home{
  background-image: url('../../assets/banner/whoarewe.png');
  background-size: cover;
  height: 58vh;
  width:100%;
  display: flex;
  justify-content: center;
  align-items: center; 
 }
 
 .nav-item ul li > a:hover {color: rgba(255,0,0,1);}

